@import url('https://fonts.googleapis.com/css2?family=Parisienne&display=swap'); /* Import an Art-deco font from Google Fonts */

.homePage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse; /* reverse the order of elements */
  background-color: rgba(211, 241, 237, 0.4);; /* change the background color to beige */
  padding: 20px; /* add padding */
}




.homePageLeft {
  width: 50%;
}

.homePageRight {
  width: 50%;
  margin-top: 20px; /* add space at the top */
}

.profilePic {
  width: 60%; /* Adjust to the desired size */
  height: auto;
  display: block;
  margin: auto; /* center the image */
}

.homeText {
  width: 60%; /* Adjust this to change line length */
  margin: 0 auto;
  text-align:justify;
  font-size: 18px;
}

.homeTitle {
  font-size: 60px;
  margin-left: 165px; /* Add margin to the left */
  width: 60%;
}



.contactButton {
  background-color: #0c0b0b;
  color: #f5eeee;
  border: none;
  padding: 20px 0; /* change the horizontal padding to 0 */
  text-align: center;
  text-decoration: none;
  display: block; /* make the button a block element */
  font-size: 18px; /* make the text larger */
  margin: 20px 0; /* center the button and add space around it */
  cursor: pointer;
  transition: background-color 0.3s ease; /* for a smooth color transition */
  width: 20%; /* adjust this to your desired width */
  margin-left: 20%; /* align left with text */
}


.contactButton:hover {
  background-color: #f5eeee;
  color: #0c0b0b; /* swap text and background color on hover */

}
