.skillsPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: #f8f9fa;
  }
  
  .skillsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .skillCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #cccccc;
    border-radius: 8px;
    padding: 20px;
    width: 200px;
    height: 200px;
  }
  
  .skillLogo {
    width: 100px;
    height: 100px;
  }
  
  .skillsPage h1 {
    color: #030303;
    font-size: 60px;
  }
  