.navigation {
  position: sticky;
  top: 0;
  z-index: 9999;
  background-color: rgba(39, 38, 38, 0.8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;

  overflow-x: auto; /* makes it horizontally scrollable when overflowed */
  
}


.navigation .name {
  font-weight: bold;
  font-size: 25px;
  color: #f5f1f1;
}

.navigation .navLinks {
  display: flex;
  list-style-type: none;
  margin-right: 50px;
  padding: 0;
  font-size: 25px;
  font-weight: bold;
}



 

.navigation .navLinks li {
    margin: 10px 0;
  }



.navigation .navLinks li {
  margin-left: 20px;
}

.navigation .navLinks li a {
  text-decoration: none;
  color: #f5eeee;
}

.navigation .navLinks li a:hover {
  color: #f0e3e3;
}

