.projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    padding: 2em;
  }

  .Projectstitle {
    font-size: 60px;
    font-weight: bold;
    margin-bottom: 30px;
    text-align: center;     /* Add this */
    width: 100%;            /* Add this */
  }
  
  .project-container {
    width: 500px;
    height: 350px;
    margin: 20px;
    overflow: hidden;
    position: relative;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  }
  
  .project-container:hover {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  }
  
  .project-container a {
    display: block;
    height: 100%;
    position: relative;
  }
  
  .project-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
  }
  
  .project-container:hover .project-image {
    transform: scale(1.1);
  }
  
  .project-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.6);
    overflow: hidden;
    width: 100%;
    height: 0;
    transition: .5s ease;
    text-align: center;
    padding: 20px;
    color: white;
  }
  
  .project-container:hover .project-overlay {
    height: 100%;
  }
  
  .project-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    white-space: pre-wrap;
  }
  