.contact-me-container {
  width: 100%;
  min-height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  font-family: Arial, Helvetica, sans-serif;
  color: #444;    /* Add this for a more professional color */
  background-color: #f9f9f9;    /* Add this for a light background color */
}

.contact-me-container h1 {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 30px;
  color: #333;    /* Add this for a darker, more professional color */
}

.contact-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;    /* Change this from center to start */
  width: 80%;
  max-width: 800px;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
  padding: 20px;    /* Increase this for more space */
  font-size: 1.3rem;    /* Increase this for larger text */
}

.info-item h2 {
  margin-bottom: 10px;
  font-size: 1.5rem;    /* Add this for larger, more prominent headers */
  color: #333;    /* Add this for a darker, more professional color */
}

.info-item p {
  margin: 0;
}

.info-item a {
  color: #0066ff;
  text-decoration: none;
}

.info-item a:hover {
  text-decoration: underline;
}
